import * as React from "react"
import PropTypes from "prop-types"
import FooterBottom from "./FooterBottom"
import { GatsbyImage } from "gatsby-plugin-image"
import { Link } from "gatsby"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { BgImage } from "gbimage-bridge"
import { faEnvelope } from "@fortawesome/free-solid-svg-icons/faEnvelope"
import { faPhone } from "@fortawesome/free-solid-svg-icons/faPhone"
import { faGlobe } from "@fortawesome/free-solid-svg-icons/faGlobe"
import { FaIcon } from "./FaIcon"

const Footer = ({ siteTitle }) => {
  let data = useStaticQuery(graphql`
    query {
      backgroundImageFooter: file(
        relativePath: { eq: "background/footer-bg.png" }
      ) {
        childImageSharp {
          gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
        }
      }

      logo: allFile(filter: { name: { in: ["logo-2"] } }) {
        edges {
          node {
            childImageSharp {
              gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP, AVIF]
                width: 320
                height: 68
              )
            }
          }
        }
      }
    }
  `)

  return (
    <footer className="main-footer padd-top">
      <BgImage
        image={getImage(data.backgroundImageFooter)}
        style={{ position: "none", opacity: 1 }}
      >
        <div className="auto-container">
          {/* <!--Widgets Section--> */}
          <div className="widgets-section">
            <div className="row clearfix">
              <div className="col-lg-4 col-md-6 col-sm-12 footer-column">
                <div className="footer-widget ">
                  <div className="logo-widget">
                    <div className="logo">
                      <Link to="/">
                        <GatsbyImage
                          image={getImage(
                            data.logo.edges[0].node.childImageSharp
                          )}
                          alt="Logo"
                        />
                      </Link>
                    </div>
                    <div className="text">
                      TonMix is an exclusive multi-purpose 100% responsive
                      Template Theme with powerful features. Simple and
                      well-structured coding, high quality and flexible layout,
                      scalable features along with color schemes to create
                      tailor-cut websites.
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-lg-4 col-md-6 col-sm-12 footer-column">
                <div className="footer-widget widget_get_in_touch">
                  <div className="links-widget">
                    <h4>Ponete en contacto</h4>
                    <ul className="list-style-two">
                      <li>
                        <FaIcon
                          icon={faEnvelope}
                          fill={"#f05523"}
                          className="icon"
                          width={18}
                          height={18}
                        />
                        Email:
                        <a href="mailto:info@tonmix.com.ar">
                          {" "}
                          info@tonmix.com.ar
                        </a>
                      </li>

                      <li>
                        <FaIcon
                          icon={faPhone}
                          fill={"#f05523"}
                          className="icon"
                          width={18}
                          height={18}
                        />
                        Telfono: <a href="tel:2087-9593">2087-9593</a>
                      </li>

                      <li>
                        <FaIcon
                          icon={faGlobe}
                          fill={"#f05523"}
                          className="icon"
                          width={18}
                          height={18}
                        />
                        Lunes a Viernes: De 9 a 18:30hs
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 footer-column">
                <div className="footer-widget ">
                  <div className="request-widget">
                    <h4>Obten tu presupuesto</h4>
                    <a className="phone" href="tel:2087-9593">
                      2087-9593
                    </a>

                    <div className="text">
                      Acced a nuestro cotizador y recib tu presupuesto
                    </div>

                    <Link to="/cotizador" className="btn-style-six theme-btn">
                      <span className="txt">Cotizar</span>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </BgImage>
      <FooterBottom />
    </footer>
  )
}

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
}

export default Footer
